.about_section {
  padding: 60px 0;

  background-color: #fff5f6;

  &::after {
    position: absolute;
    z-index: 1;
    content: '';
    top: 250px;
    left: 50%;
    transform: translateX(-50%);
    @media screen and (min-width: 1280px) {
      background-image: url(../images/about/bg-img-desktop-min.png);
      width: 1280px;
      height: 521px;
      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/about/bg-img-desktop-2x-min.png);
      }
    }
    // @media screen and (min-width: 780px) {
    //   background-image: url(../images/about/bg-img-tablet-min.png);
    //   width: 768px;
    //   height: 521px;
    //   @media (min-device-pixel-ratio: 2),
    //     (min-resolution: 192dpi),
    //     (min-resolution: 2dppx) {
    //     background-image: url(../images/about/bg-img-tablet-2x-min.png);
    //   }
    // }
    @media screen and (max-width: 767px) {
      background-image: url(../images/about/bg-img-mobile-min.png);
      width: 320px;
      height: 320px;
      top: 585px;
      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/about/bg-img-mobile-2x-min.png);
      }
    }

    background-repeat: no-repeat;
    background-size: contain;
  }
}

.container_about {
  position: relative;
  z-index: 2;
  @media screen and (min-width: 320px) {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.about_title-box {
  text-align: center;
  margin-bottom: 60px;
}

.about_subtitle {
  margin: 0;
  font-family: 'Titan One', cursive;
  font-style: normal;
  font-weight: 400;

  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffa5ba;
  @media screen and (min-width: 320px) {
    font-size: 18px;
    line-height: 1.16;
  }
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.about_tittle {
  margin: 16px 0 0;
  font-family: 'Titan One', cursive;
  font-weight: 400;

  text-align: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #d41443;
  @media screen and (min-width: 320px) {
    font-size: 34px;
    line-height: 1.14;
  }
  @media screen and (min-width: 768px) {
    font-size: 48px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.13;
  }
}

.about_content {
  @media screen and (min-width: 768px) {
    display: flex;
  }

  max-width: 100%;
  margin: 0;
}

.about_item {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 32px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 139px;
  }
}

.about_img {
  @media screen and (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 768px) {
    max-height: 335px;
  }
  @media screen and (min-width: 1280px) {
    max-height: 501px;
  }
}

.about_list {
  padding: 0;
  margin: 0;
  list-style: none;
  @media screen and (max-width: 767px) {
    max-width: 220px;
  }
}

.about_text {
  max-width: 463px;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.94;
  letter-spacing: 0.04em;
  color: #907e82;
  & > p {
    margin-bottom: 2em;
  }
  @media screen and (min-width: 320px) {
    font-size: 12px;
    line-height: 1.62;
  }
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.75;
  }
  @media screen and (min-width: 1280px) {
    line-height: 1.92;
  }
}

.about_text-bold {
  margin-bottom: 32px;
  color: #000000;

  @media screen and (min-width: 320px) {
    font-size: 12px;
    line-height: 1.62;
  }
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.75;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}

.about_btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 176px;
  height: 44px;
  border-radius: 22px;
  padding: 0;
  background-color: #ffffff;
  color: #d41443;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (max-width: 767px) {
    font-size: 14px;
    line-height: 1.28;
  }
  &:hover,
  &:focus {
    cursor: pointer;
    background-color: #ecebeb;
  }
}
.about_btn-arrow {
  position: absolute;

  top: 42%;
  right: 25px;
}
